*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

:root {
  --primary-background-color: #e6e6e6;
  --secondary-background-color: #bdc3d5;
  --primary-text-color: #213457;
  --secondary-text-color: #213457;
  --scrollbar-color: rgba(124, 137, 175, 0.4);
  --scrollbar-track-color: #5b7099;
  --transparent-color-01: rgba(0, 0, 0, 0.1);
  --box-shadow: 0px 0px 20px rgb(0 0 0 / 10%);
  --box-shadow-card: 0px 4px 15px 6px rgba(161, 170, 196, 0.4);
  --icon-background: #212121;
  --error-color: #b20d0d;
  --like-color: #3b639c;
  --span-element-color: #fefefe;
  --valid-color: #157725;
  --invalid-color: #b60c35;
  --secondary-border-color: #49608c;
  --card-background: #d9d9d9;
  --invalid-text-color: #a6b3ce;
  --color-card-text-color: #515151;
  --table-border-color: #49608c;
  --table-text-header-color: #e6e6e6;
  --border-principle-gradient: rgba(145, 156, 180, 0.4);
  --word-logo-color: rgb(13%, 20%, 34%, 0.5);
  --logo-example: #bdc3d5;
  --icons-invalid-color: #c03f4c;
}

.dark {
  --primary-background-color: #bdc3d5;
  --secondary-background-color: #213457;
  --primary-text-color: #e6e6e6;
  --like-color: var(--primary-background-color);
  --box-shadow-card: 0px 4px 15px 6px #a1aac4;
  --table-border-color: #213457;
}
.dark .social-list__link {
  color: var(--primary-background-color);
}
.dark .modal__body {
  background-color: var(--primary-text-color);
}
.dark .modal__body-button {
  color: var(--secondary-text-color);
  border-color: var(--secondary-text-color);
}
.dark .modal__body-button:hover {
  color: var(--primary-text-color);
  background-color: var(--secondary-background-color);
}
.dark .header__logo-link {
  color: var(--primary-background-color);
}
.dark .color--border-light::before {
  outline-color: transparent;
}
.dark .color--border-dark::before {
  outline-color: var(--secondary-text-color);
}
.dark .button--usage-radius {
  color: var(--primary-text-color);
  background-color: var(--secondary-text-color);
  border-color: transparent;
}
.dark .button--usage-radius:hover {
  color: var(--secondary-text-color);
  background-color: transparent;
  border-color: var(--secondary-text-color);
}
.dark .logo-wrapper--light,
.dark .icon-wrapper--light {
  border-color: var(--secondary-text-color);
}
.dark .logo-wrapper--different,
.dark .icon-wrapper--different {
  color: var(--primary-background-color);
}
.dark .logo-wrapper__element,
.dark .icon-wrapper__element {
  color: var(--secondary-border-color);
}
.dark .logo-wrapper--invalid-color,
.dark .icon-wrapper--invalid-color {
  color: var(--icons-invalid-color);
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url(".././fonts/Montserrat-Regular.woff2") format("woff2"), url(".././fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat"), url(".././fonts/Montserrat-SemiBold.woff2") format("woff2"), url(".././fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Srbija Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url(".././fonts/SrbijaSans-Regular.woff2") format("woff2"), url(".././fonts/SrbijaSans-Regular.woff") format("woff");
  font-display: swap;
}
.page {
  min-height: 100%;
  font-size: 18px;
  scroll-behavior: smooth;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
  color: var(--primary-text-color);
  letter-spacing: 0.03em;
  background-color: var(--primary-background-color);
  transition: color 0.5s ease, background-color 0.5s ease;
  scrollbar-width: auto;
  scrollbar-color: var(--scrollbar-track-color) var(--scrollbar-color);
}

.page-body::-webkit-scrollbar {
  width: 8px;
}

.page-body::-webkit-scrollbar-track {
  background: var(--scrollbar-color);
}

.page-body::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-track-color);
  border: 3px solid transparent;
  border-radius: 20px;
}

.container {
  width: 1280px;
  padding: 0 60px;
  margin: 0 auto;
}
.container--inner {
  position: relative;
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 97px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0;
}

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%);
  }
  33% {
    transform: translate(-50%, -50%) rotate(15deg);
  }
  66% {
    transform: translate(-50%, -50%) rotate(-15deg);
  }
}
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  10% {
    transform: translateX(5px);
  }
  20% {
    transform: translateX(-5px);
  }
  30% {
    transform: translateX(5px);
  }
  40% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  60% {
    transform: translateX(-5px);
  }
  70% {
    transform: translateX(5px);
  }
  80% {
    transform: translateX(-5px);
  }
  90% {
    transform: translateX(5px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.title {
  font-family: "Srbija Sans", sans-serif;
  font-size: 5rem;
  font-weight: 400;
  line-height: 0.889em;
  letter-spacing: normal;
}
.title--middle {
  font-size: 3.5rem;
  line-height: 0.952em;
}
.title--small {
  font-size: 2.2222rem;
  line-height: 1.25em;
}

.theme-button {
  position: fixed;
  top: 203px;
  right: 5px;
  z-index: 2;
  width: 72px;
  height: 72px;
  padding: 0;
  overflow: hidden;
  color: var(--primary-text-color);
  cursor: pointer;
  background: none;
  border: 2px solid var(--primary-text-color);
  border-radius: 10px;
  transition: color 0.3s ease, background-color 0.3s ease;
}
.theme-button:hover, .theme-button:focus-visible {
  color: var(--secondary-background-color);
  background-color: var(--primary-text-color);
}
.theme-button:focus-visible {
  outline: none;
}

.theme-button__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transition: transform 0.5s ease;
  transform: translate(-50%, -50%);
}

.theme-button__icon--dark {
  transform: translate(90%, -50%);
}

.theme-button--dark .theme-button__icon--dark {
  transform: translate(-50%, -50%);
}
.theme-button--dark .theme-button__icon--light {
  transform: translate(-190%, -50%);
}

.button {
  position: relative;
  padding: 18px 50px;
  font-size: 0.8889rem;
  font-weight: 600;
  line-height: 150%;
  color: var(--primary-text-color);
  text-align: center;
  letter-spacing: 0.03em;
  cursor: pointer;
  background-color: transparent;
  border: 3px solid var(--primary-text-color);
  border-radius: 30px;
  transition: color 0.5s ease, background-color 0.5s ease, border-color 0.5s ease, opacity 0.5s ease;
}
.button:hover {
  color: var(--secondary-background-color);
  background-color: var(--primary-text-color);
}
.button:focus-visible {
  outline: 1px dashed var(--primary-text-color);
}
.button:disabled {
  pointer-events: none;
  opacity: 0.7;
}
.button--active {
  pointer-events: auto;
  opacity: 1;
}
.button--usage-radius {
  display: inline-block;
  padding: 19px 66px;
  font-size: 1.2222rem;
  color: var(--secondary-text-color);
}
.button--usage-radius:hover {
  color: var(--primary-background-color);
  background-color: var(--secondary-text-color);
}

.header {
  padding: 55px 0 45px 0;
  margin: 0 0 70px 0;
  background-color: var(--secondary-background-color);
  transition: background-color 0.5s ease;
}

.header__top {
  display: grid;
  grid-template-columns: minmax(87px, min-content) 1fr;
  gap: 30px;
  margin-bottom: 20px;
}

.header__menu {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 68px;
  justify-self: end;
}

.header__logo-link {
  line-height: 0;
  color: var(--primary-text-color);
}
.header__logo-link[href] {
  position: relative;
  transition: opacity 0.5s ease;
}
.header__logo-link[href]:hover {
  opacity: 0.7;
}
.header__logo-link[href]:focus {
  outline: none;
}
.header__logo-link[href]:focus-visible {
  outline: 1px dashed var(--primary-text-color);
}

.header__menu-link {
  position: relative;
  font-size: 0.8889rem;
  font-weight: 600;
  line-height: 150%;
  color: var(--primary-text-color);
  letter-spacing: 0.03em;
  transition: color 0.5s ease;
}
.header__menu-link::before {
  position: absolute;
  bottom: -5px;
  left: 50%;
  width: 0;
  height: 1px;
  content: "";
  background-color: var(--primary-text-color);
  opacity: 0;
  transition: width 0.5s ease, opacity 0.5s ease;
  transform: translateX(-50%);
}
.header__menu-link--active::before, .header__menu-link:hover::before {
  width: 100%;
  opacity: 1;
}
.header__menu-link:focus-visible {
  border: none;
  outline: 1px dashed var(--primary-text-color);
}

.header__bottom {
  display: grid;
  grid-template-columns: 1fr 412px;
  gap: 188px;
  align-items: center;
  min-height: 372px;
}
.header__bottom--micro-grid {
  gap: 20px;
}

.header__title {
  margin: 0 0 36px 0;
}

.header__text {
  margin: 0;
}
.header__text--score {
  max-width: 582px;
}

.main-navigation,
.main-navigation-styles {
  margin: 0 0 70px 0;
}

.main-navigation__title {
  margin: 0 0 50px 0;
  color: var(--secondary-text-color);
}

.navigation-list {
  display: grid;
  grid-template-rows: repeat(1, 1fr);
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.navigation-list__item {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
  min-height: 331px;
  padding: 30px 40px 50px 40px;
  background-color: var(--secondary-background-color);
  border-radius: 30px;
  transition: background-color 0.5s ease;
}

.navigation-list__title,
.navigation-list__text {
  margin: 0;
  word-wrap: break-word;
}

.navigation-list__text {
  flex: 1;
  align-self: flex-start;
}

.navigation-list__link {
  padding: 14px 40px;
  font-size: 0.7222rem;
  letter-spacing: 0.03em;
  border-radius: 30px;
}

.navigation-list--components {
  margin: 0 0 30px 0;
}

.feedback {
  margin: 0 0 120px 0;
}

.feedback__wrapper {
  padding: 50px 60px 80px 60px;
  background-color: var(--secondary-background-color);
  border-radius: 30px;
  transition: background-color 0.5s ease;
}

.feedback__title {
  margin: 0 0 40px 0;
}

.feedback__text {
  margin: 0 0 50px 0;
}

.form-area {
  display: grid;
  grid-template-rows: repeat(3, auto);
  gap: 36px;
}

.form-area__fieldset {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  padding: 0;
  margin: 0;
  border: none;
}

.form-area__input,
.form-area__comment {
  padding: 21px 46px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  line-height: 150%;
  color: var(--secondary-text-color);
  letter-spacing: 0.03em;
  background-color: var(--card-background);
  border: 2px solid var(--scrollbar-track-color);
  border-radius: 30px;
  transition: border-color 0.5s ease, color 0.3s ease;
}
.form-area__input::placeholder,
.form-area__comment::placeholder {
  transition: opacity 0.5s ease;
}
.form-area__input:hover::placeholder, .form-area__input:focus::placeholder,
.form-area__comment:hover::placeholder,
.form-area__comment:focus::placeholder {
  opacity: 0.7;
}
.form-area__input:focus,
.form-area__comment:focus {
  border-color: var(--primary-text-color);
  outline: none;
}
.form-area__input:invalid,
.form-area__comment:invalid {
  color: var(--error-color);
}

.form-area__comment {
  min-height: 176px;
  resize: none;
}

.form-area__button {
  justify-self: center;
}

.social-list {
  display: none;
  gap: 40px;
  justify-content: center;
}

.social-list__icon {
  transition: transform 0.5s ease;
}

.social-list__link {
  position: relative;
  display: block;
  margin: 0;
  line-height: 0;
  color: var(--footer-icon-color);
  transition: color 0.5s ease, margin 0.5s ease;
}
.social-list__link:hover {
  margin: 0 10px;
}
.social-list__link:hover svg {
  transform: scale(1.2);
}
.social-list__link:focus-visible {
  outline: 1px dashed var(--primary-text-color);
}

.footer {
  padding: 30px 0 40px 0;
  background-color: var(--secondary-background-color);
  transition: background-color 0.5s ease;
}

.footer__social-list {
  margin-bottom: 60px;
}
.footer__social-list--active {
  display: flex;
}

.footer__info {
  display: flex;
  gap: 40px;
  justify-content: space-between;
}

.footer__info-item {
  margin: 0;
  font-size: 0.8889rem;
  font-weight: 600;
  line-height: 150%;
  letter-spacing: 0.03em;
}

.footer__info-item--purpose {
  max-width: 270px;
}

.footer__info-item--developer {
  max-width: 150px;
}

.footer__info-item--design {
  max-width: 154px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  visibility: hidden;
  background-color: var(--transparent-color-01);
  backdrop-filter: blur(20px);
  opacity: 0;
  transition: 0.5s ease;
}

.modal__body {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: flex-end;
  max-width: 590px;
  min-height: 708px;
  padding: 25px 30px 78px 30px;
  overflow: hidden;
  color: var(--secondary-text-color);
  background-color: var(--primary-background-color);
  border-radius: 20px;
  box-shadow: var(--box-shadow);
  transition: transform 0.5s ease;
  transform: translateY(35px);
}
.modal__body::after {
  position: absolute;
  top: -50%;
  left: 50%;
  width: 697px;
  height: 697px;
  content: "";
  background-color: var(--secondary-background-color);
  border-radius: 50%;
  transform: translateX(-50%);
}

.modal__icon-container {
  position: absolute;
  top: 118px;
  left: 50%;
  z-index: 2;
  color: var(--like-color);
  transform: translateX(-50%);
}

.modal__body-title,
.modal__body-text {
  margin: 0;
}

.modal__body-text {
  text-align: center;
}

.modal__body-button {
  margin-top: 20px;
}

.modal.modal--active {
  pointer-events: auto;
  visibility: visible;
  opacity: 1;
}
.modal.modal--active .modal__body {
  transform: translateY(0);
}

.main-inner {
  color: var(--secondary-text-color);
}

.main-inner__section {
  margin: 0 0 70px 0;
}
.main-inner__section:last-child {
  margin: 0 0 120px 0;
}

.main-inner__title {
  margin: 0 0 40px 0;
}

.main-inner__subtitle {
  margin: 0 0 20px 0;
}

.main-inner__text {
  margin: 0;
}
.main-inner__text--first {
  margin: 0 0 10px 0;
}

.main-inner__element {
  padding: 12px 26px;
  font-size: 0.8889rem;
  font-weight: 600;
  line-height: 150%;
  color: var(--span-element-color);
  letter-spacing: 0.03em;
  background-color: var(--valid-color);
  border-radius: 50px;
}
.main-inner__element--invalid {
  background-color: var(--invalid-color);
}
.main-inner__element--big {
  padding: 14px 31px;
  font-size: 1.2222rem;
}

.inner-navigation {
  position: sticky;
  top: 60px;
  align-self: start;
  padding: 33px 52px 41px 29px;
  border: 2px solid var(--secondary-text-color);
  border-radius: 20px;
}

.nav-list__item:not(:last-child) {
  margin: 0 0 24px 0;
}

.nav-list__button {
  position: relative;
  display: flex;
  gap: 15px;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2222rem;
  font-weight: 600;
  line-height: 150%;
  color: var(--secondary-text-color);
  text-align: left;
  letter-spacing: 0.03em;
  cursor: pointer;
  background: none;
  border: none;
}
.nav-list__button::after {
  width: 15px;
  height: 15px;
  content: "";
  background: url("../img/arrow.svg") center no-repeat;
  background-size: contain;
  transition: transform 0.5s ease;
  transform: rotate(-90deg);
}
.nav-list__button:hover::after, .nav-list__button.active::after {
  transform: rotate(0);
}
.nav-list__button:focus {
  outline: none;
}
.nav-list__button:focus-visible {
  outline: 1px dashed var(--secondary-text-color);
}

.nav-list__accordion {
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.5s ease;
}
.nav-list__accordion.active {
  grid-template-rows: 1fr;
}

.inner-nav {
  padding: 0 0 0 31px;
  overflow: hidden;
}

.inner-nav__item:not(:last-child) {
  margin: 0 0 12px 0;
}
.inner-nav__item:not(:last-child):first-child {
  margin: 24px 0 12px 0;
}

.inner-nav__link {
  position: relative;
  font-size: 1rem;
  color: var(--secondary-text-color);
}
.inner-nav__link::before {
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 0;
  height: 1px;
  content: "";
  background-color: var(--secondary-text-color);
  opacity: 0;
  transition: width 0.5s ease, opacity 0.5s ease;
  transform: translateX(-50%);
}
.inner-nav__link:hover::before, .inner-nav__link.active::before {
  width: 100%;
  opacity: 1;
}
.inner-nav__link:focus {
  outline: none;
}
.inner-nav__link:focus-visible {
  outline: 1px dashed var(--secondary-text-color);
}

.important-color__purpose {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 30px;
  align-items: center;
}

.important-color__example {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
  padding: 33px 46px;
  border: 3px solid var(--table-border-color);
  border-radius: 30px;
  transition: border-color 0.5s ease;
}

.principles__text:last-of-type {
  margin: 0;
}

.principles__example {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  padding: 30px 69px 57px 69px;
  border: 3px solid var(--table-border-color);
  border-radius: 30px;
  transition: border-color 0.5s ease;
}

.principles__example-block {
  display: flex;
  flex-direction: column;
  gap: 27px;
  align-items: flex-start;
}

.principles__example-block-element {
  align-self: stretch;
  padding: 42px;
  background-color: var(--card-background);
  border-radius: 10px;
}

.principles__example-text {
  font-size: 1.2222rem;
  font-weight: 600;
  line-height: 150%;
  letter-spacing: 0.03em;
}
.principles__example-text--invalid {
  color: var(--invalid-text-color);
}

.color {
  align-self: start;
  padding: 24px 28px;
  border-radius: 20px;
  box-shadow: var(--box-shadow-card);
  transition: box-shadow 0.5s ease;
}
.color::before {
  display: block;
  width: 100%;
  height: 109px;
  margin: 0 0 9px 0;
  content: "";
  background-color: var(--block-color);
  border-radius: 14px;
  transition: outline-color 0.5s ease;
}
.color--border-light::before {
  outline: 1px solid var(--secondary-background-color);
}
.color--border-dark::before {
  outline: 1px solid transparent;
}
.color--big::before {
  height: 124px;
}

.color__text {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  font-size: 0.8889rem;
  line-height: 150%;
  color: var(--color-card-text-color);
  letter-spacing: 0.03em;
}

.color__code {
  font-size: 1rem;
  line-height: 150%;
  color: var(--secondary-text-color);
  letter-spacing: 0.03em;
}

.main-colors__list {
  padding: 30px 0 0 22px;
  margin: 0;
}

.main-colors__list-item:not(:last-child) {
